.App {
  text-align: center;
  
}

.container-edit{
  background:#d8e2dc;
  margin:auto;
  margin-top: 90px;
  border-radius:3px;
  width: 400px;
  height:620px;
  color: white;
  font-family: 'Arial';
  font-size: 50px;
  display:grid;
  grid-template-areas: 
  "photo-edit"
  "display-view"
  "btn-container"
  "like-counter"
  "social links";
}
.photo-edit{
 height: 100px;
 margin:auto;
}

.image-edit{
  height: 170%;
  width: auto;
  border-radius: 100%;
  margin-top: 20px;
}

.display-view{
margin: auto;
margin-top:90px;
text-align: center;
height: 80%;

}

.btn-container{
  display: flex;

}

.btn-editor{
  height: 3em;
  width: 6em;
  background-color: #fec5bb;
  color:white;
  border-radius: 3px;
  border: none;
  justify-content: space-evenly;
  padding: 0.5em;
  font-size: 18pt;
  font-weight: 700;
  margin:auto;
}

.btn-editor:hover{
  background-color:#fcd5ce;
  
}

.like-counter{
  display: flex; 
  justify-content: space-around;
  height: 90px;

}

.like-counter button{
  height: 3em;
  width: 6em;
  background-color: #fec5bb;
  color:white;
  border-radius: 3px;
  border: none;
  justify-content: space-evenly;
  padding: 0.5em;
  font-size: 18pt;
  font-weight: 700;
  margin:auto;
}



.clear-logo{
  margin:auto;
  width: 7%;
 
}

.social-links{
 display: flex; 
 justify-content: space-evenly;
 

}

.logo-image{
  width: 35px;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

